@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/libs";
@import "general/fonts";
@import "general/base";
@import "../blocks/blocks";

.top-header {
  height: 400px;
  background: url(../img/header-bg.jpeg) 50% 0px / cover no-repeat;
  background-attachment: fixed;
  &_overlay {
    width: 100%;
    height: 100%;
    background: rgba(12, 24, 36, 0.75);
  }
}

.container {
  @include container();
}

.col-5 {
  @include col();
  @include size-md(5);
  @include size-sm(12);
  @include size-xs(12)
}

.col-6 {
  @include col();
  @include size-md(6);
  @include size-sm(6);
  @include size-xs(12)
}

.col-7 {
  @include col();
  @include size-md(7);
  @include size-sm(12);
  @include size-xs(12)
}

.col-12 {
  @include col();
  @include size-md(12);
}

.row {
  @include row-flex(); // () ne zabivai
}

.white-block {
  background-color: #ffffff;
}

.shadow-block {
  box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.10), 0px 1px 6px rgba(0, 0, 0, 0.20);
}

.p50 {
  padding: 20px;
  @include sm-block() {
    padding: 30px;

  }
  @include md-block() {
    padding: 50px;

  }
}

.about-img-content {
  text-align: center;
}

.section-content {
  margin-top: 20px;
  @include sm-block() {
    margin-top: 35px;
  }
  @include md-block() {
    margin-top: 50px;
  }
}

.about-section {
  margin-top: -250px;
}

.about-img {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  background: url("../img/avatar.jpg") center top / cover no-repeat;
  border-radius: 50%;
  @include sm-block() {
    width: 160px;
    height: 160px;

  }
  @include md-block() {
    width: 230px;
    height: 230px;

  }
}

.about-name {
  margin: 25px 0 0;
  font-family: 'Dosis', 'Open Sans', 'Sans Serif';
  font-size: 2.9rem;
  font-weight: 700;
  letter-spacing: 5px;
  text-transform: uppercase;
  color: #0EB57D
}

.about-title {
  padding: 15px 0;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 3px;
}

.about-section-title {
  position: relative;
  font-family: 'Dosis', 'Open Sans', 'Sans Serif';
  font-size: 2.4rem;
    font-weight: 700;
  letter-spacing: 5px;
  text-transform: uppercase;
  line-height: 1.7;
  &:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: -10px;
    display: inline-block;
    width: 70px;
    height: 3px;
    background: #0EB57D;
    border-radius: 30px;
  }
}

p {
  margin-bottom: 0;
  font-family: 'Open Sans', 'Sans Serif';
  font-weight: 400;
  font-size: 1.5rem;

  line-height: 1.8;
  color: #666666;
}

.about-data {
  margin: 30px 0 0;
}

.about-data > div {
  margin-top: 20px;
  font-size: 14px;
}

.about-data > div:first-of-type {
  margin-top: 30px;
}

.about-data > div > span {
  display: inline-block;
  width: 80px;
  font-weight: 700;
}

.section-title h2 {
  margin: 40px 0 0;
  font-size: 2.8rem;
   @include sm-block() {
     margin: 60px 0 0;


  }
  @include md-block() {
    margin: 100px 0 0;


  }
  font-weight: 700;
  letter-spacing: 5px;
  text-transform: uppercase;
  text-align: center;
}

.progress-bar-wrapper {
  margin-top: 30px;
}

.progress-bar-wrapper:first-of-type {
  margin-top: 0;
}

.progress-text {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
  font-size: 1.5rem;
  font-weight: 600;
  text-align: left;
  text-transform: capitalize;
  color: #555555;
}

.progress-bar {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 5px;
  background: #e6e6e6;
  border-radius: 10px;
}

.progress-bar > span {
  display: block;
  width: 80%;
  height: 100%;
  background-color: #0EB57D;
}

.highlight {
  margin-bottom: 16px;
  background-color: #f8f8f8;
  border-radius: 5px;
}

.highlight pre {
  padding: 16px;
  overflow: auto;
  font-size: 12px;
  line-height: 1.45;
  background-color: #f6f8fa;
}

.copyright-text {
  margin-top: 50px;
  width: 100%;
  padding: 20px 0;
  font-family: 'Dosis', 'Open Sans', 'Sans Serif';
  font-size: 1.4rem;
  font-weight: 600;
  letter-spacing: 3px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background: #0EB57D;
}
.scroll-code{
  overflow-x: scroll;
}