html{
	font-size: 6px;
	@include sm-block() {
		font-size: 8px;


	}
	@include md-block() {
		font-size: 10px;


	}
}
body {
	font-family: "Sans Serif", "Arial", sans-serif;
	line-height: 1;
	color: #555555;
	background-color: #e6e6e6;
	}
*{
	box-sizing: border-box;
}
h1, h2, h3, h4, h5, h6, ul {
	padding: 0;
	margin: 0;
}

.container {
	@include container();
	@include sm(max-width, 510px);
	@include md(max-width, 690px);
	@include lg(max-width, 930px);
	@include xl(max-width, 1024px);
}

.img-responsive {
	max-width: 500px;
	width: 100%;
	height: auto;
}